@import url("https://fonts.googleapis.com/css2?family=Libre+Barcode+128+Text&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Aleo:ital,wght@0,100..900;1,100..900&family=Libre+Barcode+128+Text&display=swap");
h1 {
  font-family: "Libre Barcode 128 Text", system-ui;
  font-weight: 400;
  font-style: normal;
  font-size: 3.25rem;
}

h2 {
  font-weight: 350;
}

a {
  color: gray;
  text-emphasis: none;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

.skill {
  margin: 0.5em;
  padding: 0.5em;
  border-radius: 5px;
  color: white;
  margin-left: 0;
}

.skillList {
  margin-top: 0.5em;
  padding: 0.5em;
  padding-left: 0;

  display: flex;
  flex-wrap: wrap;
}

p {
  font-size: 1.2rem;
}

#articles {
  display: flex;
  text-decoration: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  max-width: 80%;
  max-width: 900px;
  margin: auto;
}

.details {
  width: 90%;
  align-items: flex-start;
}

.article p {
  padding-left: 0;
}

.article {
  margin-top: 1.5em;
}

#container {
  padding: 2em;
  display: grid;
  grid-template-columns: 1fr 5fr;
  max-width: 1200px;
  margin: auto;
}

#buttons {
  margin: 2em;
  margin-left: 0;
  font-size: 3rem;
}

#buttons :hover {
  text-decoration: underline;
}

#hasInsert {
  display: flex;
  flex-direction: row;
}

#insert {
  margin: 1em;
  margin-left: 0;
  padding: auto;
}

#mapDemo {
  min-width: 400px;
  min-height: 500px;
}

iframe {
  margin-left: 0;
}

@media screen and (max-width: 700px) {
  h1 {
    margin: 0.75em;
  }
  .details {
    margin-top: 0.5em;
    margin: auto;
  }
  #container {
    padding: 1em;
    display: flex;
    flex-direction: column;
  }

  #articles {
    justify-self: center;
    justify-content: center;
    align-items: center;
    align-content: center;
    width: fit-content;
  }

  #buttons {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    margin: 0.5em;
  }

  #buttons h2 {
    font-weight: 350;
    font-size: 1.25rem;
  }

  a {
    font-weight: 200;
  }

  .article {
    margin: 1em;
    margin-top: 2em;
  }

  #hasInsert {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  #mapDemo {
    min-width: 280px;
    min-height: 375px;
    width: 100%;
  }

  .skill {
    font-size: 0.6rem;
  }

  .skillList {
    margin-top: 0.25em;
    padding: 0.25em;
    flex-wrap: wrap;
  }
}
