@import url("https://fonts.googleapis.com/css2?family=Libre+Barcode+128+Text&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Aleo:ital,wght@0,100..900;1,100..900&family=Libre+Barcode+128+Text&display=swap");
h1 {
  font-family: "Libre Barcode 128 Text", system-ui;
  font-weight: 400;
  font-style: normal;
  font-size: 3.25rem;
}

a#contact {
  color: rgb(40, 40, 40);
  line-height: 1.4rem;
}

#home {
  overflow-x: hidden;
  display: flex;
  height: 100vh;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  transform-style: preserve-3d;
}

p {
  font-family: "Aleo", serif;
  font-optical-sizing: auto;
  font-weight: 350;
  font-style: normal;
  font-size: 1.5rem;
  color: rgb(45, 45, 45);
}

#about {
  font-weight: 320;
  font-size: 2rem;
  width: 40%;
  max-width: 70%;
  min-width: 275px;
  color: black;
}

#arrows {
  display: flex;
  justify-content: space-evenly;
  width: 70%;
  min-width: 300px;
}

.arrow {
  display: flex;
}

.arrow :hover {
  text-decoration: underline;
}

.arrow svg {
  margin: auto;
  transform: scale(0.7);
}
.arrow svg path {
  fill: rgb(40, 40, 40);
}

@media only screen and (max-width: 450px) {
  p {
    font-size: 1.2rem;
  }
}
