@import url("https://fonts.googleapis.com/css2?family=Libre+Barcode+128+Text&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Aleo:ital,wght@0,100..900;1,100..900&family=Libre+Barcode+128+Text&display=swap");
h1 {
  font-family: "Libre Barcode 128 Text", system-ui;
  font-weight: 400;
  font-style: normal;
  font-size: 3.25rem;
}

* {
  font-family: "Aleo", serif;
}

p {
  font-size: 1rem;
}

#resume {
  height: 100vh;
  transform-style: preserve-3d;
}

.eduItem {
  padding-top: 0.5em;
  text-align: left;
}

.eduItem h3 {
  font-size: 1.1rem;
  margin: 0;
}

.eduItem p {
  margin: 0;
}

#resume #body {
  display: grid;
  grid-template-columns: 3fr 1fr;
  width: 75%;
  min-width: 550px;
  margin: auto;
  padding: 1em;
}

.panel {
  border: 1px rgb(0, 0, 0) dashed;
  margin-top: 2em;
  min-height: 225px;
  min-width: 250px;
}

.panel ul li {
  margin: 0.5em;
  font-weight: 300;
}

.panel ul {
  margin: 2em;
}

#panelHolder {
  top: 0;
}

h2 {
  margin: 0.5em;
  margin-left: 0;
  font-size: 1.75rem;
  text-align: left;
  margin-bottom: 0;
}

ul {
  margin: 0;
  padding: 0;
}

li {
  height: fit-content;
}

.vertical {
  width: 100%;
  margin: auto;
}

.section {
  margin: 0.5em;
  padding: 1em 2em 1em 2em;
}

.section .top {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.top p {
  float: right;
  padding: 0.5em;
  padding-left: 0;
  text-align: right;
  margin: auto;
  margin-right: 0;
  font-size: 1.15rem;
}

.role {
  float: left;
  width: 100%;
  text-align: left;
}

.role p {
  margin: 0.25em;
  font-size: 1.3rem;
}

.details {
  display: flex;
  flex-direction: column;
}

.tasks {
  margin: 0.5em;
  text-align: left;
  line-height: 1.7rem;
}

@media only screen and (max-width: 650px) {
  h2 {
    font-size: 1.3rem;
    margin: 0;
    padding: 0;
  }
  .top p {
    font-size: 0.8rem;
  }

  .role p {
    font-size: 1.1rem;
  }

  #resume #body {
    display: flex;
    flex-direction: column;
    width: 85%;
    min-width: 280px;
    padding: 0.5em;
  }

  .panel {
    padding: 1em;
    width: 50%;
    margin: auto;
    margin-top: 2em;
    min-width: 200px;
    min-height: 150px;
  }

  #panelHolder {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .section {
    padding: 1em;
  }
}

@media only screen and (max-width: 1000px) {
  #resume #body {
    width: 95%;
  }
  h2 {
    font-size: 1.5rem;
    margin: 0;
    padding: 0;
  }
}
